<template>
  <div class="login-wrap">
    <div class="title">
      <img class="title-logo" src="https://img.yzcdn.cn/vant/logo.png">
      <h2 class="title-text">百杰票务</h2>
      <p class="title-desc">星巴克、肯德基等生活服务类</p>
      <p class="title-desc">礼品卡、代金券、权益交易平台</p>
    </div>
    <van-cell-group>
      <van-field 
        v-model="tel" 
        placeholder="请输入手机号" 
        label="手机号" 
        type="tel" 
        left-icon="phone-o"
        clearable 
        maxlength='11'
        autocomplete="off"
      />
      <van-field 
        v-model="password" 
        placeholder="请输入密码" 
        label="密码" 
        type="password" 
        left-icon="closed-eye"
        autocomplete="off"
      />
    </van-cell-group>
    <div class="tip">
      <span class="fl">还没有账号？</span>
      <a class="green" @click="goRegister">立即注册</a>
      <a class="fr green" @click="goForgetPassword">忘记密码</a>
    </div>
    <van-button type="primary" block @click="login">登录</van-button>
  </div>
</template>

<script>
export default {
  name: 'login',
  data () {
    return {
      tel: '',
      password: ''
    }
  },
  methods: {
    goRegister () {
      this.$router.push({ path: '/register' })
    },
    goForgetPassword () {
      this.$router.push({ path: '/forgetPassword' })
    },
    isValid () {
      if (!this.tel) {
        this.$toast.fail('请输入手机号')
        return false
      }
      if (!this.tel.match(/^1\d{10}$/)) {
        this.$toast.fail('请输入正确的手机号')
        return false
      }

      return true
    },
    login () {
      // if (!this.isValid) {
      //   return false
      // }
      // this.$http.login({
      //   tel: this.tel,
      //   password: this.password
      // }).then(res => {
      //   if (res.code === 0) {
          this.$toast.success('登录成功')

          setTimeout(() => {
            this.$router.push({ path: '/index' })
          }, 1000)
      //   } else {
      //     this.$toast.fail(res.message)
      //   }
      // }).catch(err => {
      //   console.log(err)
      // })
    }
  }
}
</script>



